//Colors
$colorPrimary: #f8c300;
$colorPrimaryShaded: #faa620;
$colorIcons: #e2e2e2;
$colorBackground: #fff;
$colorBackgroundShaded: #f7f7f7;
$colorBlack: #000;

// UV COlors
$clrUvLow: #558b2f;
$clrUvMod: #f9a825;
$clrUvHigh: #ef6c00;
$clrUvVeryHigh: #b71c1c;
$clrUvExtreme: #6a1b9a;

// Transition
$transition: all 0.5s ease-in-out;

$colors: (
  "primary": $colorPrimary,
  "primaryDark": $colorPrimaryShaded,
  "trail": $colorPrimary,
  "trailDark": $colorPrimaryShaded,
);

:export {
  @each $name, $value in $colors {
    #{unquote($name)}: $value;
  }
}
